import uniSegmentedControl from "@/components/uni-segmented-control.vue";
import helper from '../../common/helper.js';

export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			State: '1', //初始任务状态值
			lqArray: '', //领取数组
			num: '6', //默认显示9条任务数据
			Tishi:''
		}
	},
	onLoad() {
		this.fllist();
	},
	onShow() {
		this.fllist()
	},
	//下拉刷新
	onPullDownRefresh: function() {
		this.fllist(true);
		uni.showLoading({
			title: "刷新中....",
			mask: true
		})
	},
	//触底事件
	onReachBottom() {
		var tas1 = new String('5'); //触底加载5条数据
		this.num = Number(this.num) + Number(tas1);
		this.fllist();
	},
	methods: {
		// 获取已领取任务的id
		openlq(e) {
			uni.navigateTo({
				url: '../task/YilinquTask?lqid=' + e.currentTarget.dataset.lqid
			});
		},
		// 请求任务列表
		fllist(isdown) {
			helper.islogin(true);
			uni.request({
				url: helper.websiteUrl + 'task/getmytasklist',
				method: 'GET',
				data: helper.postdata({
					"page": "1",
					"num": this.num,
					"State": this.State,
					"userid": helper.getstate().userid,
				}),
				success: res => {
					if (res.data.code == 200) {
							//日期循环格式化  直接绑定不能显示
							res.data.data.baseinfo.forEach((e) => {
								e.Createdate = e.Createdate.substr(5, 5);
							});
							this.lqArray = res.data.data.baseinfo;
					} else {
						helper.goout(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
					if (res.data.data.baseinfo.length>=this.num) {       //判断触底加载是否到底最后一条 否则提示没有更多数据了
						this.bottomtisi = '正在加载...'
					} else{
						this.bottomtisi = '没有更多数据了'
					}
					 if(res.data.data.baseinfo.length === 0){            //判断分类数据是否为空 否则提示暂无任务
						this.Tishi ="暂无任务";
						this.bottomtisi = ''
					}else {
						this.Tishi ="";
						
					}
					uni.hideLoading();
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请下拉刷新重试！'
					});
				},
				complete: () => {
					if (isdown) {
						uni.stopPullDownRefresh(); //刷新停止
					}
				}
			});
		},
		
	}
}
